import BasicForm from "./BasicForm.jsx";
import styles from "./Auth.module.css";
import Form from "../../components/Form/Form";
import {addNotification, toggleLoader} from "../../redux/actions/uiActions";
import {updateCurrentUser, updateAdminPrivilege} from "../../redux/actions/userActions";
import Weebee from "../../model/user";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import {authenticator} from "../../firebaseConfig";
import {PathMapper, Genres} from "../../constant";

class LoginForm extends BasicForm{
  constructor(props){
    super(props);
    
    this.login = this.login.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  cancel(){
    this.updateEmail("");
    this.updatePassword("");
  }

  login(){
    if(this.state.email && this.state.password){
      this.props.toggleLoader(true);
      authenticator.signInWithEmailAndPassword(
        this.state.email,
	this.state.password
      )
      .then(response => {
        this.props.updateAdminPrivilege(this.state.email, this.state.password);
        this.props.updateCurrentUser(new Weebee(response));
	this.updateFormSubmissionStatus(true);
      })
      .catch(err => {
        this.props.addNotification(
	  Math.random(),
	  {
	    notification:err.message,
	    title:err.code.split("/").reverse()[0]
	  }
	);
      })
      .finally(() => {
        this.props.toggleLoader(false);
      });
    }
  }

  render(){
    return (
      !this.state.formSubmitted ?
      <Form
        id={styles.loginForm}
	title=""
	hint="Welcome"
	miniTitle="Sign in to your account"
	onSubmit = {this.login}
	onCancel = {this.cancel}
	inputs={[
	  {
	    icon:"fa-user",
	    placeHolder:"Email",
	    value:this.state.email,
	    onInput:this.updateEmail,
	  },
	  {
	    icon:"fa-key",
	    placeHolder:"Password",
	    value:this.state.password,
	    onInput:this.updatePassword,
	    type:"password"
	  }
	]}
      />
      : <Redirect to={PathMapper.main(this.props.hash, Genres[0].id)} />
    );
  };
};

function mapStateToProps(state){
  var userState = state.user;
  return {
    hash:userState.userHash
  };
};

function mapDispatchWithProps(dispatch){
  return {
    addNotification:(id, notification) => dispatch(addNotification(id, notification)),
    updateAdminPrivilege: (email, password) => dispatch(updateAdminPrivilege(email, password)),
    updateCurrentUser: user => dispatch(updateCurrentUser(user)),
    toggleLoader : value => dispatch(toggleLoader(value))
  };
}

export default connect(mapStateToProps, mapDispatchWithProps)(LoginForm);
