import styles from './App.module.css';
import React from "react";
import Navbar from './components/Navbar/Navbar';
import {connect} from "react-redux";
import {Route, Redirect} from "react-router-dom";
import Auth from "./pages/Auth/Auth";
import Main from "./pages/Main/Main";
import NotificationContainer from "./components/NotificationContainer/NotificationContainer";
import {PathMapping as urlPaths, firestore} from "./constant";
import "video.js";
import Loader from "./components/Loader/Loader";
import Dash from "./pages/Dash/Dash";
import PostBlog from "./pages/PostBlog/PostBlog";
import {database} from "./firebaseConfig";
import {fromDB} from "./redux/actions/blogActions";
import AnimeBlog from "./model/blog";

class App extends React.Component{
  
  componentDidMount(){
    var updateRedux = dbValue => {
      var databaseValues = dbValue.val();
      if(databaseValues){
        this.props.fromDB(Object.values(databaseValues).map(json => new AnimeBlog(json)));
      }
    };
    database.ref(firestore.blog).get().then(snapShot =>{
      updateRedux(snapShot);
    });
    database.ref(firestore.blog).on('value', snapShot => {
      updateRedux(snapShot);
    });
  }

  render(){
    return (
      <div 
	    id={styles.anime_world}
	    className={`${this.props.lightTheme? "": "dark"} ${this.props.sideMenuOpen ? styles.sideMenuOpen : ""}`}
	    style={{"--logo-color": this.props.logoColor}}
      >
        <Navbar
        />
	<NotificationContainer className={styles.notificationContainer}/>
	<Route exact path={urlPaths.none} component={props => <Redirect to="/auth" />} />
	<Route path={urlPaths.auth} component={Auth} />
	<Route path={urlPaths.main} component={Main} />
	<Route path={urlPaths.dash} component={Dash} />
	<Route path={urlPaths.newBlog} component={PostBlog} />
	<Loader show={this.props.showLoader} />
      </div>
    )
  }
};

function mapStateToProps(state){
	state = state.ui;
	return {
		lightTheme:state.lightTheme,
		sideMenuOpen:state.sideMenuOpen,
		logoColor:state.logoColor,
		showLoader:state.showLoader
	};
}

function mapDispatchWithProps(dispatch){
  return {
    fromDB: blogs => dispatch(fromDB(blogs))
  };
}

export default connect(mapStateToProps, mapDispatchWithProps)(App);
