import React from "react";
import styles from "./Notification.module.css";
import {connect} from "react-redux";
import {removeNotification} from "../../redux/actions/uiActions";

class Notification extends React.Component{
  constructor(props){
    super(props);
    const DURATION=60;
    this.state = {
      isOpen:true,
      isMinimized:this.props.minimize,
    };
    this.id = this.props.id;

    this.minimize = this.minimize.bind(this);
    this.close = this.close.bind(this);
    this.timeout = setTimeout(this.close.bind(this), DURATION*1e3);
  }

  componentWillUnmount(){
    this.timeout && clearTimeout(this.timeout);
    this.timeout =null;
  }

  close(){
    this.timeout && this.setState({
      isOpen:false
    }, () => {
      this.props.removeNotification(this.id);
      this.timeout = null;
    });
  }

  minimize(){
    this.setState({
      isMinimized:!this.state.isMinimized
    });
  }

  render(){
    return (
      this.state.isOpen ?
      <div className={`${styles.notification} ${this.state.isMinimized ? styles.minimized : ""}`}>
        <div className={styles.menu}>
	  <div className={styles.title}>{this.props.title}</div>
	  <div className={styles.options}>
	    <div id={styles.min} onClick={this.minimize} className={styles.opt}>-</div>
	   <div id={styles.close} onClick={this.close} className={styles.opt}>x</div>
	  </div>
	</div>
	<div className={styles.content}>
	  <div>{this.props.notification}</div>
	</div>
      </div>
      : null
    );
  }
};

function mapDispatchToProps(dispatch){
  return {
    removeNotification:value => dispatch(removeNotification(value))
  };
};

export default connect(null, mapDispatchToProps)(Notification);
