const DEFAULT_BLOG = {
  createdAt: new Date().toISOString(),
  createdBy:"",
  blog:"",
  title:"",
  likes:[],
  comments:[],
  genre:"",
  videoLinks:[],
  imageLinks:[],
};

export default class AnimeBlog{
  constructor(blogProps=DEFAULT_BLOG){
    blogProps = Object.assign({}, DEFAULT_BLOG, blogProps);
    this.id = blogProps.id || Date.now();
    this.createdAt = typeof(blogProps.createdAt) === "string" ? new Date(blogProps.createdAt) : blogProps.createdAt;
    this.createdBy = blogProps.createdBy;
    this.title=blogProps.title;
    this.blog = blogProps.blog;
    this.likes =blogProps.likes;
    this.genre = blogProps.genre;
    this.comments = blogProps.comments;
    this.videoLinks = blogProps.videoLinks;
    this.imageLinks = blogProps.imageLinks;

    this.likeIt = this.likeIt.bind(this);
    this.commentIt = this.commentIt.bind(this);
    this.unlikeIt = this.unlikeIt.bind(this);
    this.toJSON = this.toJSON.bind(this);
  }

  get likesCount(){
    return this.likes.length;
  }

  get commentsCount(){
    return Object.keys(this.comments).length;
  }

  get assets(){
    return this.imageLinks.map(
      image => ({ ...image })
    )
    .concat(
      this.videoLinks.map(
        video => ({url:video, video:true})
      )
    );
  }

  get postedOn(){
    return this.createdAt.toString().split("GMT")[0];
  }

  likeIt(userMail){
    this.likes.push(userMail);
  }

  commentIt(userMail, comment){
    this.comments.push({
      user:userMail,
      comment,
      time:new Date().toISOString()
    });
  }

  unlikeIt(mail){
    this.likes = this.likes.filter(m => m!== mail);
  }

  toJSON(){
    return {
      title:this.title,
      createdBy:this.createdBy,
      createdAt:this.createdAt,
      id:this.id,
      blog:this.blog,
      comments:this.comments,
      likes:this.likes,
      imageLinks:this.imageLinks,
      videoLinks:this.videoLinks,
      genre:this.genre
    };
  }

};
