import React from "react";
import styles from "./Tooltip.module.css";

function ToolTipAdded(ChildComponent){
    return class extends React.Component{
        render(){
	    return (
	        <div className={`${styles.toolTip} ${this.props.className ||""}`}>
		    <ChildComponent {...this.props} />
		    <div className={`${styles.toolTipText} ${styles[this.props.position || "top"]}`}>{this.props.toolTipText}</div>
		</div>
	    );
	};
    };
};

export default ToolTipAdded;
