import React from "react";
import styles from "./Dash.module.css";
import {connect} from "react-redux";
import Icon from "../../components/Icons/Icon";
import {Redirect} from "react-router-dom";
import {PathMapping} from "../../constant";
import {toggleSideMenu} from "../../redux/actions/uiActions";

class Dash extends React.Component{
  componentDidMount(){
    this.props.toggleSideMenu(false);
  }

  render(){
    return (
      this.props.isAnonymous ? 
      <Redirect to={PathMapping.auth} /> :
      <div id={styles.profileContainer}>
        <div id={styles.profileCard}>
	  <div className={styles.avatar}>
	    <div className={styles.avatarBg} style = {{ backgroundImage:`url("${this.props.currentUser.photoURL}")` }}></div>
	    <img src={this.props.currentUser.photoURL} className={styles.avatar} alt={this.props.currentUser.displayName}/>
	  </div>
	  <Icon
	    faw={true}
	    icon="fa-user"
	    iconTitle={this.props.currentUser.displayName}
	  />
	  <Icon
	    faw={true}
	    icon="fa-envelope"
	    iconTitle={this.props.currentUser.email}
	  />
	  <Icon
	    faw={true}
	    icon={`fa-phone ${this.props.currentUser.phoneNumber ? "" : styles.empty}`}
	    iconTitle={this.props.currentUser.phoneNumber}
	  />
	  <div className={`${styles.emailVerified} ${this.props.currentUser.emailVerified ? styles.yes : styles.no}`}>
	    <Icon
	      faw={true}
	      icon="fa-check"
	      iconTitle="Email verified"
	    />
	  </div>
	</div>
      </div>
    );
  }
};

function mapStateWithProps(state){
  var userState = state.user;
  return {
     currentUser:userState.currentUser,
     isAnonymous: userState.isAnonymous
  }
};

function mapDispatchToProps(dispatch){
  return {
    toggleSideMenu: value => dispatch(toggleSideMenu(value))
  };
}

export default connect(mapStateWithProps, mapDispatchToProps)(Dash);
