import md5 from "md5";

export const userActions = {
  adminUser:"ADMIN_PRIVILEGE",
  updateUser:"UPDATE_CURRENT_USER"
},
      updateAdminPrivilege = (email, password) => ({
        type: userActions.adminUser,
	data:{
	  email:md5(email),
	  password:md5(password)
	}
      }),
      updateCurrentUser = user => ({
        type:userActions.updateUser,
	data:user
      });
