import React from "react";
import styles from "./Blog.module.css";
import Icon from "../Icons/Icon";
import Tooltip from "../../hoc/Tooltip/Tooltip";
import Video from "../Video/Video";
import Marquee from "../Marquee/Marquee";
import Comments from "./Comments";
import {connect} from "react-redux";
import {updateBlog} from "../../redux/actions/blogActions";
import {firestore} from "../../constant";
import {database} from "../../firebaseConfig";
import {addNotification} from "../../redux/actions/uiActions";

const BlogMeta = props => {
  return (
    <Marquee text={(p) => <div className={styles.blogMeta}>Posted by <div className={styles.auth}>{props.blog.createdBy}</div> on {props.blog.postedOn}</div>} />
  );
};

class Blog extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      showComment:false,
      currentUserComment:"",
      isLiked:false,
      isCommented:false,
    };
    this.tooltipBlogMeta = Tooltip(BlogMeta);
    this.toggleCommentDisplay = this.toggleCommentDisplay.bind(this);
    this.updateCurrentUserComment = this.updateCurrentUserComment.bind(this);
    this.__checkLikeAndCommentStatus = this.__checkLikeAndCommentStatus.bind(this);
    this.onDBError = this.onDBError.bind(this);
    this.onComment = this.onComment.bind(this);
    this.addComment = this.addComment.bind(this);
    this.onLike = this.onLike.bind(this);

  }

  __checkLikeAndCommentStatus(){
    if(this.props.blog && this.props.currentUser){
      var isLiked = this.props.blog.likes.indexOf(this.props.currentUser.email) !== -1,
          isCommented = this.props.blog.comments.map(comment => comment.user||"").indexOf(this.props.currentUser.email) !== -1
      isLiked !== this.state.isLiked && this.setState({
        isLiked
      });
      isCommented !== this.state.isCommented && this.setState({
        isCommented
      });
    }
  }
  
  componentDidMount(){
    this.__checkLikeAndCommentStatus();
  }

  onDBError(Err){
    if(Err){
      var id =Math.random();
      this.props.addNotification(id, {
        id,
	notification:Err.message,
	title:Err.code
      });
    } else{
    }
  }

  componentDidUpdate(prevProps){
    this.__checkLikeAndCommentStatus();
  }

  addComment(){
   if(this.state.currentUserComment && !/^\s*$/.test(this.state.currentUserComment)){
     this.props.blog.commentIt(this.props.currentUser.email, this.state.currentUserComment);
     this.updateCurrentUserComment("");
     this.props.updateBlog(this.props.blog);
     database.ref(`${firestore.blog}/${this.props.blog.id}`).set(
       this.props.blog.toJSON(),
       (Err) => this.onDBError(Err)
     );
   }
  }

  onComment(e){
    e.preventDefault();
    switch(e.keyCode){
      case 13:
        this.addComment();
        break;
      default:
        break;
    }
  }

  updateCurrentUserComment(value){
    this.setState({
      currentUserComment:value
    });
  }

  onLike(){
    if(this.props.isAnonymous){
      return;
    }
    if(!this.state.isLiked){
      this.props.blog.likeIt(this.props.currentUser.email);
    }else{
      this.props.blog.unlikeIt(this.props.currentUser.email);
    }
    this.props.updateBlog(this.props.blog);
    database.ref(`${firestore.blog}/${this.props.blog.id}`).set(
      this.props.blog.toJSON(),
      (err) => this.onDBError(err)
    );
  }

  toggleCommentDisplay(){
    !this.props.isAnonymous && this.setState({
      showComment:!this.state.showComment
    });
  }

  render(){
    return (
      <div
        className={`${styles.blog} ${this.props.className||""}`}
      >
        <div className={styles.titleAssetContainer}>
          <div className={styles.title}>{this.props.blog.title}</div>
  	  <div className={styles.assets}>
	    {
	      this.props.blog.assets.map((asset, index) => {
	        return (
	        <div className={styles.asset} key={index}>
	          <div className={styles.bg} style={{backgroundImage:`url("${asset.video ? "video/c1" : asset.url}")`}}>
		  </div>
	          {
		  asset.video ? 
	            <Video src={asset.url} />
		    : <img src={asset.url} alt={`Asset ${index}`}/>
		  }
	        </div>
	        );
	      })
	    }
	  </div>
	</div>
	<div className={styles.details}>
	  <div className={styles.meta}>
	    <Icon
	      faw={true}
	      icon={this.state.isLiked? "fa-heart" : "fa-heart-o"}
	      iconTitle={this.props.blog.likesCount}
	      onClick={this.onLike}
	    />
	    <Icon
	      faw={true}
	      icon={this.state.isCommented ? "fa-comment" :"fa-comment-o"}
	      iconTitle={this.props.blog.commentsCount}
	      onClick={this.toggleCommentDisplay}
	    />
	    <this.tooltipBlogMeta blog={this.props.blog} toolTipText={this.props.blog.postedOn} position="bottom"/> 
	  </div>
	  {
	    this.state.showComment ? 
	    <Comments 
	      comment={this.state.currentUserComment}
	      onInput={this.updateCurrentUserComment}
	      onKeyPress ={this.onComment}
	      placeHolder="Enter to post the comment"
	      onIconClick={this.addComment}
	      comments={this.props.blog.comments} />
	    :
	    <div 
	      className={styles.content} 
	      onClick={this.toggleContent}
	    >{this.props.blog.blog}</div>
	  }
	</div>
      </div>
    );
  }
};

function mapStateWithProps(state){
  var userState = state.user;
  return {
    currentUser: userState.currentUser,
    isAnonymous: userState.isAnonymous
  };
}

function mapDispatchToProps(dispatch){
  return {
    updateBlog: blog => dispatch(updateBlog(blog)),
    addNotification:(id, data) => dispatch(addNotification(id, data)),
  };
}

export default connect(mapStateWithProps, mapDispatchToProps)(Blog);
