import React from "react";
import Icon from "../Icons/Icon";
import styles from "./Fab.module.css";
import {Link} from "react-router-dom";

class Fab extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      fabClosed:true
    };

    this.toggleFab = this.toggleFab.bind(this);
  }

  toggleFab(){
    this.setState({
      fabClosed: this.props.items ? !this.state.fabClosed : true
    }, () => {
      this.props.onClick && this.props.onClick(this.state.fabClosed);
    });
  }

  render(){
    return (
    <div
      className={
        `${styles.fab} ${!this.state.fabClosed ? styles.show : ""} ${this.props.items ? styles.fabWithMenu : ""} ${this.props.className || ""}`
	}
      onClick={this.toggleFab}
      onBlur={this.toggleFab}
    >
      <div className={styles.title}>
        <Icon
	  icon="fa-plus"
	  faw={true}
	/>
      </div>
      {
        this.props.items ? 
        <div className={styles.fabMenu}>
	  <div className={styles.menuContainer}>
	  {
	    (this.props.items || []).map(item => {
	      return item.isLink ? 
	      <Link key={Math.random()} to={item.path} className={styles.anchor}>
	        <Icon {...item} />
	      </Link> :
	      <Icon key={Math.random()} {...item} />;
	    })
	  }
	  </div>
	</div>
	: null
      }
    </div>
    );
  }
};

export default Fab;
