import React from "react";

export default class BasicForm extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      email:"",
      password:"",
      formSubmitted:false
    }

    this.updateEmail = this.updateEmail.bind(this);
    this.updatePassword = this.updatePassword.bind(this);
    this.updateFormSubmissionStatus =this.updateFormSubmissionStatus.bind(this);

  };

  updateEmail(value){
    this.setState({
      email:value.toLowerCase(),
    });
  }

  updatePassword(value){
    this.setState({
      password:value
    });
  }

  updateFormSubmissionStatus(value){
    this.setState({
      formSubmitted:value
    });
  }
};
