import React from "react";
import styles from "./IconInput.module.css";
import Input from "../Input/Input";
import Icon from "../Icons/Icon";

class IconInput extends React.Component{
  constructor(props){
    super(props);
    
    this.updateValue = this.updateValue.bind(this);
  }

  updateValue(value){
      this.props.onInput && this.props.onInput(value);
  }

  render(){
    return (
      <div className={`${styles.iconInput} ${this.props.className || ""}`}>
        <Input onInput={this.updateValue} placeHolder={this.props.placeHolder} type={this.props.type} value={this.props.value} accept={this.props.accept} multiple={this.props.multiple} onKeyPress={this.props.onKeyPress} onPaste={this.props.onPaste}/>
	<Icon faw={true} icon={this.props.icon} onClick = {this.props.onIconClick}/>
      </div>
    );
  }
};

export default IconInput;
