import React from "react";
import styles from "./ToggleIcon.module.css";

export default class ToggleIcon extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isOn:this.props.value
        };

        this.toggleIcon = this.toggleIcon.bind(this);
    }

    toggleIcon(){
        this.setState({
            isOn: !this.state.isOn
        }, () => {
            this.props.onToggle && this.props.onToggle(this.state.isOn);
	});

    }

    render(){
        return (
            <label className={`${this.props.className || ""} ${styles.tgSwitch}`}>
                <input type="checkbox" value={this.state.isOn} onChange={this.toggleIcon}/>
                <span className={`${styles.slider} ${this.state.isOn ? styles.active : ""}`}></span>
            </label>
        );
    }
}
