import { uiActions } from "../actions/uiActions";
import {LogoColors} from "../../constant";

const uiState = {
	lightTheme:true,
	sideMenuOpen:false,
	logoColor:LogoColors.saiyan,
	notifications:{},
	showLoader:false
};

export default function uiReducer(uiState={}, action){
	var newState = Object.assign({}, uiState);
	switch(action.type){
		case uiActions.themeToggle:
			newState.lightTheme = action.data;
			break;
		case uiActions.sideMenuToggle:
			newState.sideMenuOpen = action.data;
			break;
		case uiActions.updateLogoColor:
			newState.logoColor= action.data;
			break;
		case uiActions.addNotification:
			var value=action.data;
			newState.notifications[value.id] = value.value;
			break;
		case uiActions.removeNotification:
			delete newState.notifications[action.data];
			break;
		case uiActions.removeAllNotifications:
			newState.notifications = {};
			break;
		case uiActions.toggleLoader:
			newState.showLoader = action.data;
			break;
		default:
			break;
	}
	return newState;
};

export {
	uiState
};
