import React from "react";
import styles from "./Card.module.css";

const ModalCard = ChildComponent => {
  return class extends React.Component{

    render(){
      return (
        <div 
	  className={`${styles.card} ${styles.modal} ${this.props.active || this.props.active === undefined?styles.activeModal : ""} ${this.props.className||""}`}
	>
	  <ChildComponent {...this.props} />
	</div>
      );
    }
  };
};

export default ModalCard;
