import React from "react";
import styles from "./Main.module.css";
import {connect} from "react-redux";
import DualPage from "../../hoc/DualPage/DualPage";
import Fab from "../../components/Fab/Fab";
import BlogContainer from "./BlogContainer";
import ModalCard from "../../hoc/Card/ModalCard";
import VideoContainer from "./VideoContainer";
import {PathMapping, AnonymousUser, PathMapper} from "../../constant";
import {authenticator} from "../../firebaseConfig";
import {addNotification} from "../../redux/actions/uiActions";
import {updateCurrentUser, updateAdminPrivilege} from "../../redux/actions/userActions";
import {Redirect} from "react-router-dom";

class Main extends React.Component{

  constructor(props){
    super(props);
    
    this.state = {
      isLoggedOut:false,
      properties:{
	 aTitle:"Blog",
	 bTitle:"Videos",
	 componentA:{
	   className:styles.blogCard,
	   blogs:(this.props.blogs||[]).filter(blog => blog.genre == this.props.match.params.genre)
	 },
	 componentB:{
	   className:styles.blogCard,
	 }
      }
    };

    this.blogContainer = ModalCard(BlogContainer);
    this.videoContainer = ModalCard(VideoContainer);

    this.logout = this.logout.bind(this);
  }

  componentDidUpdate(prevProps){
    if(prevProps.match.params.genre !== this.props.match.params.genre){
      var props = Object.assign({}, this.state.properties);
      props.componentA.blogs = this.props.blogs.filter(blog => blog.genre == this.props.match.params.genre)
      this.setState({
        properties: props
      })
    }
  }

  logout(){
    authenticator.signOut()
    .then(() => {
      this.props.updateCurrentUser(AnonymousUser);
      this.props.addNotification(Math.random(), {
	title:"Logged out successfully",
	minimize:true
      });
      this.props.updateAdminPrivilege(AnonymousUser.hash,"");
      this.setState({
        isLoggedOut:true
      });
    })
    .catch(err => {
      this.props.addNotification(Math.random(), {
	title:"Error logging out",
	notification:err.message
      });
    });
  }

  componentDidMount(){
    this.setState({
      isLoggedOut:this.props.match.params.userId !== this.props.userHash
    });
  }

  render(){
    return (
       this.state.isLoggedOut ?
       <Redirect to={PathMapping.auth} /> :
       <div
         id={styles.main}
       >
         <DualPage
	   properties = {this.state.properties}
	   componentA={this.blogContainer}
	   componentB={this.videoContainer}
	 />
	 <Fab
	   className={styles.fab}
	   items = {[
	       this.props.isAdmin ? {
	         faw:true,
	         iconTitle:"Post",
	         icon:"fa-sticky-note-o",
		 path:PathMapping.newBlog
	       } : null,
	       !this.props.currentUser.isAnonymous ? {
	         faw:true,
		 iconTitle:"Logout",
		 icon:"fa-power-off",
		 isLink:false,
		 onClick:this.logout
	       } : null,
	       {
	         faw:true,
		 iconTitle:"Manga",
		 icon:"fa-paint-brush",
		 path:"#"
	       },
	       {
	         faw:true,
		 iconTitle: "Dashboard",
		 icon:"fa-user",
		 path:PathMapper.dash(this.props.userHash)
	       }
	   ].filter(v=>Boolean(v))
	   .map(item => ({isLink:true, ...item}))
	   }
	 />
       </div>
     );
  }
};

function mapStateToProps(state){
  var userState = state.user,
      blogState = state.blog;
  return {
    isAdmin: userState.isAdmin,
    currentUser: userState.currentUser,
    userHash:userState.userHash,
    blogs: blogState.blogs
  };
}

function mapDispatchToProps(dispatch){
  return {
    updateCurrentUser : user => dispatch(updateCurrentUser(user)),
    addNotification : (id, value) =>  dispatch(addNotification(id, value)),
    updateAdminPrivilege : (email, password) => dispatch(updateAdminPrivilege(email, password))
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);
