import React from "react";
import ToggleIcon from "../../components/ToggleIcon/ToggleIcon";
import styles from "./DualPage.module.css";

class DualPage extends React.Component{
    constructor(props){
      super(props);
      this.state = {
        showComponentA:true
      };

      this.properties = this.props.properties || {};
      this.componentA =this.props.componentA || null;
      this.componentB = this.props.componentB || null;

      this.onFormSwitch = this.onFormSwitch.bind(this);
      this.toggleComponent = this.toggleComponent.bind(this);
    }

    onFormSwitch(value){
     //nothing
    }

    toggleComponent(){
      this.setState({
        showComponentA: !this.state.showComponentA
      }, () => {
        this.onFormSwitch(this.state.showComponentA);
      });
    }

    render(){
      return (
        <div
	  className={`${styles.container} ${this.props.className||""}`}
	>
	  <div
	    className={styles.selectionContainer}
	  >
	    <span className={this.state.showComponentA ? styles.active : ""}>{this.properties.aTitle}</span>
	    <ToggleIcon value={!this.state.showComponentA} onToggle={this.toggleComponent} />
	    <span className={!this.state.showComponentA ? styles.active : ""}>{this.properties.bTitle}</span>
	  </div>
	 {
	   this.state.showComponentA ?
	   this.componentA ? <this.componentA {...this.properties.componentA} /> : null
	   :
	   this.componentB ? <this.componentB {...this.properties.componentB} /> : null
	 }
	 {
	   this.props.children || null
	 }
	</div>
      );
    }
}


export default DualPage;
