import BasicForm from "./BasicForm.jsx";
import styles from "./Auth.module.css";
import Form from "../../components/Form/Form";
import {authenticator} from "../../firebaseConfig";
import {addNotification, toggleLoader} from "../../redux/actions/uiActions";
import {updateCurrentUser} from "../../redux/actions/userActions";
import Weebee from "../../model/user";
import {connect} from "react-redux";

class SignUpForm extends BasicForm{
  constructor(props){
    super(props);
    
    this.signup = this.signup.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  cancel(){
    this.updateEmail("");
    this.updatePassword("");
  }

  signup(){
    if(this.state.email && this.state.password){
      this.props.toggleLoader(true);
      authenticator.createUserWithEmailAndPassword(
        this.state.email,
        this.state.password
      )
      .then(userCredential => {
        this.props.updateCurrentUser(new Weebee(userCredential));
	this.updateFormSubmissionStatus(true);
      })
      .catch(err => {
        var id=Math.random();
	this.props.addNotification(
	  id, 
	  {title:err.code.split("/").reverse()[0], notification:err.message, id}
	)
      })
      .finally(() => {
        this.props.toggleLoader(false);
      });
    }
  }

  render(){
    return (
      <Form
        id={styles.signupForm}
        title=""
	miniTitle="Join the Animeverse"
	hint="Join the family"
	onSubmit ={this.signup}
	onCancel={this.cancel}
	inputs={[
	  {
	    placeHolder:"Email",
	    onInput:this.updateEmail,
	    value:this.state.email,
	    icon:"fa-user"
	  },
	  {
	    placeHolder:"Password",
	    onInput:this.updatePassword,
	    icon:"fa-key",
	    value:this.state.password,
	    type:"password"
	  }
	]}
      />
    );
  }
};

function mapDispatchWithProps(dispatch){
  return {
    addNotification: (id, value) => dispatch(addNotification(id, value)),
    updateCurrentUser: newUser => dispatch(updateCurrentUser(newUser)),
    toggleLoader : val => dispatch(toggleLoader(val)),
  };
};

export default connect(null, mapDispatchWithProps)(SignUpForm);
