import React from "react";
import styles from "./PostBlog.module.css";
import {connect} from "react-redux";
import {PathMapping, imgBBAPI, firestore, Genres} from "../../constant";
import {Redirect} from "react-router-dom";
import Form from "../../components/Form/Form";
import {addNotification, toggleLoader} from "../../redux/actions/uiActions";
import {database} from "../../firebaseConfig";
import AnimeBlog from "../../model/blog";
import Chips from "../../components/Chips/Chips";
import {fromDB} from "../../redux/actions/blogActions";
import $404 from "../../assets/404.png";

class PostBlog extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      blogTitle:"",
      blogContent:"",
      images:[],
      videos:[],
      genre:Genres[0].id,
      videoLink:""
    };

    this.__generateBase64FromBlob = this.__generateBase64FromBlob.bind(this);
    this.openVideoUploadWebsite = this.openVideoUploadWebsite.bind(this);
    this.pushImagesToServer = this.pushImagesToServer.bind(this);
    this.removeBlog = this.removeBlog.bind(this);
    this.removeVideoLink = this.removeVideoLink.bind(this);
    this.updateTitle = this.updateTitle.bind(this);
    this.updateGenre = this.updateGenre.bind(this);
    this.updateContent = this.updateContent.bind(this);
    this.updateImages = this.updateImages.bind(this);
    this.updateVideoValue = this.updateVideoValue.bind(this);
    this.updateVideos = this.updateVideos.bind(this);
    this.updateVideoLinks = this.updateVideoLinks.bind(this);
    this.updateOnVideoLinkPaste = this.updateOnVideoLinkPaste.bind(this);
    this.postNewBlog = this.postNewBlog.bind(this);
    this.cancelBlog = this.cancelBlog.bind(this);
  }

  __generateBase64FromBlob(blob){
    return new Promise((resolve, reject) => {
      var fileReader = new FileReader();
      fileReader.readAsDataURL(blob);
      fileReader.onloadend = () => resolve(fileReader.result);
      fileReader.onerror = (err) => reject(err);
    });
  }

  openVideoUploadWebsite(){
    window.open("https://cloudinary.com/console/c-d9f217fc69510586547abf8efb3b2b/media_library/folders/home", "_blank");
  }

  pushImagesToServer(name, image){
    var body = new FormData();
    body.append("image", image);
    body.append("name", name);
    return fetch(`https://api.imgbb.com/1/upload?key=${imgBBAPI}`,{
      method:"POST",
      body
    });
  }

  removeBlog(blog){
    database.ref(`${firestore.blog}/${blog.id}`).remove(err => {
      if(err){
         this.props.addNotification(blog.id, 
	 {
	    id:blog.id,
	    notification:err.message,
	    title:err.code
	 });
      }else{
        this.props.updateBlog(this.props.blogs.filter(b => b.id !== blog.id));
      }
    })
  }

  removeVideoLink(link){
    this.setState({
      videos: this.state.videos.filter(l => l!== link)
    })
  }

  updateTitle(value){
    this.setState({
      blogTitle:value
    });
  }

  updateGenre(e){
    var value = e.target.value;
    this.setState({
      genre:Genres.findIndex(genre => genre.name===value)
    });
  }

  updateContent(value){
    this.setState({
      blogContent:value
    });
  }

  updateImages(value){
    this.setState({
      images:Object.assign([],value)
    });
  }

  updateVideoValue(value){
    this.setState({
      videoLink:value
    }, this.updateVideoLinks);
  }

  updateVideoLinks(){
    if(this.state.videoLink && /^(http|https):\/\/([a-zA-Z0-9]+(\.\/)*)+\.[a-z]{3,}/.test(this.state.videoLink)){
      this.setState({
         videos:Object.assign([], [this.state.videoLink, ...this.state.videos]),
	 videoLink:""
      });
    }
  }

  updateVideos(e){
    e.preventDefault();
    switch(e.keyCode){
      case 32:
        this.updateVideoLinks();
	break;
      default:
        break;
    }
  }

  updateOnVideoLinkPaste(e){
    this.updateVideoLinks();
  }

  postNewBlog(){
    if(this.state.blogTitle && this.state.blogContent){
        this.props.toggleLoader(true);
        var newImages =[],
	    images = this.state.images.map(image => {
	  var unique = Date.now(),
	      newFileName = `${unique}_${image.name}`;
	   return this.pushImagesToServer(newFileName, image)
	   .then(res => res.json())
	   .then(json => {
	     if(/2[0-9][0-9]/.test(json.status)){
	       newImages.push({
	         id:json.data.id,
		 url:json.data.url,
		 delete:json.data.delete_url
	       });
	     }else{
	       throw new Error(`Error posting ${image.name}`);
	     }
	   })
	   .catch(err => {
	     this.props.addNotification(unique, {
	       id:unique,
	       notification:err.message,
	       title:"Error posting "+image.name
	     });
	   })
	});
	   (images.length ? images.reverse() : [Promise.resolve(true)])[0].finally(()=> {
            var newBlog = new AnimeBlog({
	        title:this.state.blogTitle,
	        blog:this.state.blogContent,
	        createdBy:this.props.currentUser.email,
	        imageLinks: newImages.length ? newImages : [{url:$404}],
	        videoLinks: this.state.videos,
		genre:this.state.genre
	    });
	    database.ref(`${firestore.blog}/${newBlog.id}`).set(newBlog.toJSON())
	    .catch(err => {
	      this.props.addNotification(newBlog.id,{
	        id:newBlog.id,
		notification:err.message,
		title:"Error posting to DB"
	      });
	    })
	    .finally( ()=>{
	      this.cancelBlog();
	      this.props.toggleLoader(false);
	      });
	  })

    }
    else{
      var newId = Math.random();
      this.props.addNotification(newId, {
        notification:"Kindly enter the missing values",
	title:"Values Missing",
	key:newId
      });
    }
  }

  cancelBlog(){
    this.setState({
      blogTitle:"",
      blogContent:"",
      images:[],
      videos:[],
    })
  }

  render(){
    return (
      this.props.isAdmin ?
      <div id={styles.newAndOldBlog}>
        <Form
          id={styles.postContainer}
	  title="New blog"
	  onSubmit={this.postNewBlog}
	  onCancel={this.cancelBlog}
	  inputs={[
	    {
	      icon:"fa-pencil",
	      faw:true,
	      placeHolder:"Enter the blog title",
	      value:this.state.blogTitle,
	      onInput:this.updateTitle
	    },
	    {
	      icon:"fa-cc",
	      faw:true,
	      placeHolder:"Enter the blog content",
	      value:this.state.blogContent,
	      onInput:this.updateContent
	    },
	    {
	      type:"file",
	      icon:"fa-image",
	      accept:"image/*",
	      multiple:true,
	      onInput:this.updateImages
	    },
	    {
	      icon:"fa-video-camera",
	      onKeyPress:this.updateVideos,
	      onInput: this.updateVideoValue,
	      value:this.state.videoLink,
	      onPaste:this.updateOnVideoLinkPaste,
	      placeHolder:"Enter the video url [Press space bar or copy paste] - Click on icon to open video upload site",
	      onIconClick:this.openVideoUploadWebsite
	    },
	  ]}
        >
	  <div className={styles.genreSelect}>
	    <div className={styles.title}>Genre</div>
	    <select onChange={this.updateGenre}>
	      {
	        Genres.map((genre,id) => {
		  return <option value={genre.name} key={id}>{genre.name}</option>
		})
	      }
	    </select>
	  </div>
	</Form>
	<div className={styles.videoChips}>
	{
	  this.state.videos.map(video => {
	    return <Chips  title={video} key={Math.random()} onClose={() => this.removeVideoLink(video)}/>
	  })
	}
	</div>
	<div className={styles.blogList}>
	  {
	    (this.props.blogs||[]).map(blog => <Chips title={`${blog.title} Posted on ${blog.postedOn}`} key={blog.id} onClose={() => this.removeBlog(blog)}/>)
	  }
	</div>
      </div>
      : <Redirect to={PathMapping.auth} />
    );
  }
};

function mapStateWithProps(state){
  var userState = state.user,
      blogState = state.blog;
  return {
     isAdmin : userState.isAdmin,
     currentUser: userState.currentUser,
     blogs:blogState.blogs
  }
}

function mapDispatchToProps(dispatch){
  return {
    addNotification: (id, not) => dispatch(addNotification(id, not)),
    toggleLoader: value => dispatch(toggleLoader(value)),
    updateBlog: blogs => dispatch(fromDB(blogs)),
  };
};

export default connect(mapStateWithProps, mapDispatchToProps)(PostBlog);
