import { userActions } from "../actions/userActions";
import {AdminHash, AnonymousUser} from "../../constant";

var userState = {
  isAnonymous:AnonymousUser.isAnonymous,
  isAdmin:false,
  currentUser:AnonymousUser,
  userHash:AnonymousUser.hash
};

function userReducer(state=userState, action){
  var newState = Object.assign({}, state);
  switch(action.type){
    case userActions.adminUser:
      var requestedUserHash = action.data;
      newState.isAdmin = (
        (AdminHash.email === requestedUserHash.email) && 
	(AdminHash.password === requestedUserHash.password)
      );
      break;
    case userActions.updateUser:
      newState.currentUser = action.data;
      newState.isAnonymous = action.data.isAnonymous;
      newState.userHash = action.data.hash;
      break;
    default:
      break;
  };
  return newState;
};

export default userReducer;

export {
  userState
};
