import React from "react";
import styles from "./Video.module.css";
import videojs from "video.js"

class Video extends React.Component{
  constructor(props){
    super(props);

    this.id = `${styles.video}-${Math.floor(Math.random() * 500).toString()}`;
    this.videoDOM = React.createRef();
    this.videoPlayer = null;
  }
  componentDidMount(){
    this.videoPlayer = videojs(
      this.id,
      Object.assign({}, 
        this.props,
	{
	  autoPlay:false,
	  controls:true,
	  controlBar:{
	    seekToLive:false,
	    pictureInPictureToggle:false,
	  },
	  playbackRates:[0.25, 0.5, 1, 1.5, 2],
	  sources:[
	    {
	      src:this.props.src,
	      type:"video/mp4"
	    }
	  ]
	}
      ),
    );
  }

  componentWillUnmount(){
    if(this.videoPlayer){
      this.videoPlayer.dispose();
    }
  }
  render(){
    return (
     <video
       id={this.id}
       playsInline={true}
       className={`video-js ${styles.video}`}
       ref={this.videoDOM}
     >
       <p className="vjs-no-js">
        To view the video, Kindly enable Javascript on your browser or try installing latest browsers
       </p>
     </video>
    );
  }
};

export default Video;
