import React from "react";
import styles from "./ThemeIcon.module.css";
import ToggleIcon from "../ToggleIcon/ToggleIcon";
import {toggleTheme, updateLogoColor} from "../../redux/actions/uiActions";
import {connect} from "react-redux";
import {LogoColors} from "../../constant";

class ThemeIcon extends React.Component{
    constructor(props){
        super(props);
	this.toggleTheme = this.toggleTheme.bind(this);
    }

    toggleTheme(value){
	    this.props.toggleTheme(!value);
    }

    render(){
        return (
            <div className={styles.iconContainer}>
                <ToggleIcon
		    className={styles.themeToggler}
		    value={!this.props.lightTheme}
		    onToggle = {this.toggleTheme}
		/>
            </div>
        );
    }
};

function mapStateToProps(state){
	state = state.ui;
	return {
		lightTheme:state.lightTheme
	};
}

function mapDispatchToProps(dispatch){
	return {
		toggleTheme:(value) => {
			var logoColor=value ? LogoColors.saiyan : LogoColors.normal;
			dispatch(toggleTheme(value));
			dispatch(updateLogoColor(logoColor));
		}
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(ThemeIcon);
