import ModalCard from "../../hoc/Card/ModalCard";
import LoginForm from "./LoginForm";
import SignUpForm from "./SignUpForm";
import {connect} from "react-redux";
import {LogoColors} from "../../constant";
import {updateLogoColor} from "../../redux/actions/uiActions";
import DualPage from "../../hoc/DualPage/DualPage";
import styles from "./Auth.module.css";

class Auth extends DualPage{
  constructor(props){
    super(props);

    this.componentA = ModalCard(LoginForm);
    this.componentB = ModalCard(SignUpForm);

    this.properties = {
      aTitle:"Login",
      bTitle:"Signup",
      componentA:{className:styles.formContainer, active:this.state.showingComponentA},
      componentB:{className:styles.formContainer, active:!this.state.showingComponentA}
    };
    
    this.getLogoColor = this.getLogoColor.bind(this);
    
    this.props.updateLogoColor(this.getLogoColor());
  }

  getLogoColor(){
    return this.state.showComponentA ? LogoColors.saiyanGod : LogoColors.saiyanUltra;
  }

  onFormSwitch(value){
    this.props.updateLogoColor(this.getLogoColor());
  }

};

function mapDispatchToProps(dispatch){
  return {
    updateLogoColor:value => dispatch(updateLogoColor(value)),
  };
}

export default connect(null, mapDispatchToProps)(Auth);
