export const blogActions = {
  addBlog:"ADD_BLOG",
  fromDB:"FROM_DB",
  updateBlog:"UPDATE_BLOG",
},
  addBlog = (blog) => ({
    type:blogActions.addBlog,
    data:blog
  }),
  fromDB = allBlogs => ({
    type:blogActions.fromDB,
    data:allBlogs
  }),
  updateBlog = blog => ({
     type:blogActions.updateBlog,
     data:blog
  });
