import md5 from "md5";

export default class Weebee{
  constructor(props={}){
    this.firebaseUser = props.user || {
      displayName:"Anonymous",
      email:"anonymous@anywhere.com",
      emailVerified:false,
      isAnonymous:true,
      phoneNumber:"",
      photoURL:""
    };
    this.additionalInfo = props.additionalUserInfo || {
      isNewUser:true
    };

    this.hash = md5(this.email);
  }

  get displayName(){
    var test = /[a-zA-Z0-9.]+/.exec(this.email);
    return this.firebaseUser.displayName || (test ? test[0] : this.email);
  }

  get email(){
    return this.firebaseUser.email;
  }

  get isAnonymous(){
    return this.firebaseUser.isAnonymous;
  }

  get photoURL(){
    return this.firebaseUser.photoURL || `https://ui-avatars.com/api/?name=${this.displayName}&background=random`;
  }

  get phoneNumber(){
    return this.firebaseUser.phoneNumber;
  }

  get isNewUser(){
    return this.additionalInfo.isNewUser;
  }
};
