import {blogActions} from "../actions/blogActions";

export const blogState = {
   blogs:[]
};

export default function blogReducer(state=blogState, action){
  var newState = Object.assign({}, state);
  switch(action.type){
    case blogActions.fromDB:
      newState.blogs = action.data;
      break;
    case blogActions.updateBlog:
      newState.blogs = newState.blogs.map(b => {
        if(b.id === action.data.id){
	  return action.data;
	}else{
	  return b;
	}
      });
        break;
    default:
      break;
  }

  return newState;
};
