import React from "react";
import Icon from "../Icons/Icon";
import styles from "./Sidemenu.module.css";
import {ReactComponent as naruto} from "../../assets/naruto.svg";
import {ReactComponent as another} from "../../assets/another.svg";
import {ReactComponent as bakugan} from "../../assets/bakugan.svg";
import gintama from "../../assets/gintama.png";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import { toggleSideMenu } from "../../redux/actions/uiActions";
import ToolTipAdded from "../../hoc/Tooltip/Tooltip";
import {Genres, PathMapper} from "../../constant";

const Bars = props => {
	return (
            <div className={styles.barContainer} onClick={props.onClick}>                    {
		[1,2,3].map(key => {
			return (<div 
			    className={styles.bar} 
			    key={key}
			    style={{ backgroundColor:(props.barColor||"#000") }}
			></div>);
		})
	    }
            </div>
	);
};

class Sidemenu extends React.Component{
    constructor(props){
        super(props);
        this.sideMenu = [
            {
                icon:naruto,
            },{
                icon:bakugan,
            },{
                icon:another,
            },{
                icon:gintama,
		png:true
            }
        ].map((icon, index) => {
	  return Object.assign(
	    {},
	    icon,
	    {
	      title:Genres[index].name,
	    }
	  );
	});

	this.toggleSideMenu = this.toggleSideMenu.bind(this);
	this.getSiteURL = this.getSiteURL.bind(this);
    }

    getSiteURL(index){
      return PathMapper.main(this.props.currentHash, index);
    }

    toggleSideMenu(){
	    this.props.toggleSideMenu(!this.props.sideMenuOpen);
    }

    render(){
        var ToolTipIcon = ToolTipAdded(Icon);
        return <div
            id={styles.sidemenu}
            className={this.props.sideMenuOpen ? styles.active : ""}
        >
	    {
                 !this.props.sideMenuOpen ? 
		    <Bars 
		    	onClick={this.toggleSideMenu}
		    	barColor={this.props.lightTheme?"#fff":undefined}
		    /> : "" 
	    }
            <div className={styles.overlayMenu}>
		<div className={styles.close} onClick={this.toggleSideMenu}>
		    <i className="fa fa-2x fa-close"></i>
		</div>
		<Link className={styles.anchor} to={PathMapper.dash(this.props.currentHash)}>
		   <ToolTipIcon icon="fa-2x fa-home" faw={true} iconTitle="Home" toolTipText="Login/Dash" />
		</Link>
                {
                    this.sideMenu.map((menu, index) => {
		      return (
		        <Link className={styles.anchor} to={this.getSiteURL(index)} onClick={this.toggleSideMenu} key={index}>
		          <ToolTipIcon key={index} icon={menu.icon} iconTitle={menu.title} toolTipText={menu.title} svg={!menu.png}/>
			</Link>
		      );
		    })
                }
            </div>
        </div>
    }
};

function mapStateToProps(state){
	var userState = state.user;
	state = state.ui;
	return {
		lightTheme:state.lightTheme,
		sideMenuOpen:state.sideMenuOpen,
		currentHash: userState.userHash
	};
};

function mapDispatchToProps(dispatch){
	return {
		toggleSideMenu: value => dispatch(toggleSideMenu(value))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Sidemenu);
