import React from "react";
import styles from "./Form.module.css";
import IconInput from "../IconInput/IconInput";
import ToolTipAdded from "../../hoc/Tooltip/Tooltip";
import Button from "../Button/Button";

class Form extends React.Component{

  constructor(props){
    super(props);
    (() => {
      var H1 = props => <div className={styles.title}>{props.title}</div>
      this.titleComponent = ToolTipAdded(H1);
    })();
    
    this.onFormSubmit = this.onFormSubmit.bind(this);
    this.onFormConfirm = this.onFormConfirm.bind(this);
    this.onFormCancel = this.onFormCancel.bind(this);
  }

  onFormSubmit(e){
    e.preventDefault();
    return false;
  }

  onFormConfirm(){
    this.props.onSubmit && this.props.onSubmit();
  }

  onFormCancel(){
    this.props.onCancel && this.props.onCancel();
  }

  render(){
    return (
      <div 
        id={this.props.id}
	className={styles.formContainer}
      >
        <div className={styles.head}>
          <this.titleComponent toolTipText={this.props.hint || this.props.title} title={this.props.title} position="bottom" className={styles.toolTip}/>
 	  <div className={styles.miniTitle}>{this.props.miniTitle}</div>
	</div>
	<form className={styles.form} onSubmit={this.onFormSubmit} action="#">
	  <div className={styles.inputContainer}>
	    {
	      (this.props.inputs || []).map((input,index) => {
	        return <IconInput {...input} key={index}/>
	      })
	    }
	    {
	      this.props.children
	    }
	  </div>
          <div className={styles.buttonContainer}>
	    <Button
	      title="Submit"
	      onClick={this.onFormConfirm}
	    />
	    <Button
	      title="Cancel"
	      onClick={this.onFormCancel}
	    />
	  </div>
	</form>
      </div>
    );
  };
};

export default Form;
