import React from "react";
import styles from "./NotificationContainer.module.css";
import {connect} from "react-redux";
import Notification from "../Notification/Notification";
import Icon from "../Icons/Icon";
import {removeAllNotifications} from "../../redux/actions/uiActions";

class NotificationContainer extends React.Component{

  get notificationsAvailable(){
    return this.props.notifications && this.props.notifications.length;
  }
  render(){
    return (
      this.notificationsAvailable ?
      (
        <div className={`${styles.notificationContainer} ${this.props.className || ""}`}>
	  <div
	    className={styles.close}
	  >
            <Icon
	      faw={true}
	      icon="fa-close"
	      onClick = {this.props.clearAll}
	    />
	  </div>
          <div className={styles.notificationBox}>
            {
	      this.props.notifications.map(notification => {
	        return <Notification {...notification} key={notification.id} />
	      })
	    }
	  </div>
        </div>
      )
      : null
    );
  }
};

function mapStateWithProps(state){
  state = state.ui;
  return {
    notifications:Object.values(state.notifications)
  };
};

function mapDispatchToProps(dispatch){
  return {
    clearAll: () => dispatch(removeAllNotifications()),
  };
};

export default connect(mapStateWithProps,mapDispatchToProps)(NotificationContainer);
