export const uiActions = {
	themeToggle:"THEME_TOGGLE",
	sideMenuToggle:"SIDE_MENU_TOGGLE",
	updateLogoColor:"UPDATE_LOGO_COLOR",
	addNotification:"ADD_NOTIFICATION",
	removeNotification:"REMOVE_NOTIFICATION",
	removeAllNotifications:"REMOVE_ALL_NOTIFICATIONS",
	toggleLoader:"TOGGLE_LOADER",
},
	toggleTheme = (value) => ({
		type:uiActions.themeToggle,
		data:value
	}),
	toggleSideMenu = (value) => ({
		type:uiActions.sideMenuToggle,
		data:value
	}),
	updateLogoColor = (value) => ({
		type:uiActions.updateLogoColor,
		data:value
	}),
	addNotification = (id,value) => ({
	        type:uiActions.addNotification,
		data:{id, value:Object.assign({}, {id}, value)}
	}),
	removeNotification = value => ({
		type:uiActions.removeNotification,
		data:value
	}),
	removeAllNotifications = () => ({
		type:uiActions.removeAllNotifications
	}),
	toggleLoader = value => ({
	  type:uiActions.toggleLoader,
	  data:value
	});
